@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'DM Serif Display', serif;
  font-weight: 400;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
}
.app {
  width: 1440px;
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
}
a {
  text-decoration: none;
}
.main, .main-bottom, .footer-logo-container, .footer-links {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 10px;
}
button {
  cursor: pointer;
}
.nav-button {
    color: black;
    border: 2px solid black;
    padding: 10px 25px;
}
.nav-button:hover {
    color: white;
    background-color: hsl(256, 26%, 20%);
}
.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 30px 0;
}
.nav-list {
    display:flex;
}

.nav-list li {
    list-style: none;
    padding: 0 15px;
    text-transform: uppercase;
}
.nav-list > li > a {
    color:black;
}
.hamburger {
    display: flex;
    border: none;
    
}
.navbar img {
    cursor: pointer;
}
.hamburger-icon, .close-icon {
    display: none;
}
.nav-pattern {
    display: none;
}

@media screen and (max-width: 768px) {
    .hamburger {
        outline: 0;
    }
    .navbar .hamburger .close-icon {
        display: none;
    }
    .navbar .hamburger .hamburger-icon {
        display: block;
    }
    .navbar .hamburger.active .close-icon {
        display: block;
    }
    .navbar .hamburger.active .hamburger-icon {
        display: none;
    }
    .nav-list {
        height: 100vh;
        width: 100vw;
        display: flex;
        align-items: center;
        flex-direction: column;
        font-weight: 700;
        position: relative;
    }
    .nav-list > li > a {
        color:white;
    }
    .nav-list li {
        padding: 20px 0;
    }
    .nav-menu {
      position: fixed;
      top: -120%;
      background-color: hsl(270, 9%, 17%);
    }
    .nav-menu.active {
        top:80px;
        z-index: 100;
        
    }
    .nav-menu-button {
        color: black;
        border: 2px solid white;
        padding: 10px 25px;
    }
    .navbar {
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        
    }
    .nav-button:hover {
        background-color: transparent;
    }
    .nav-pattern {
        position: absolute;
        bottom: 85px;
    }
}
.landingtextcontainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
}

.landingtextcontainer > h1 {
    font-size: 2.8rem;
    padding: 80px 0 0;
    font-weight: 400;
    font-family: 'DM Serif Display', serif;
    text-align: center;
    
}
.landingtextcontainer > p {
    font-family: 'Karla', sans-serif;
    padding: 15px;
    font-weight: 400;
    text-align: center;
}
.landing-button {
    display: flex;
    justify-content: center;
    
}
.landing-button > button {
   font-size: 1.1rem;
    color: white;
    border: 2px solid white;
    background-color: hsl(256, 26%, 20%);
    padding: 10px 25px;
}
.landing-button > button:hover {
    color: hsl(256, 26%, 20%);
    background-color: white;
}
.landingtext {
    text-align: center;
    padding: 40px 0;
}
.image-family-desktop > img {
    display: none;
}
.landingpage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: hsl(256, 26%, 20%);
    color: white;
    
}
.landing-button {
    padding: 50px;
}
.pattern1 > img {
    position: absolute;
    top: 0;
    left: 0;
}
.pattern2 > img {
    right: 0;
    bottom: -200px;
    position: absolute;
}
.header-pattern-right {
    display: none;
}
@media screen and (min-width: 768px) {
    .pattern1 > img {
        display: none;
    }
    .pattern2 > img {
        display: none;
    }
    .header-pattern-right {
        display: block;
    }
    .landingtext {
        text-align: left;
    }
    .landingtext > h1 {
        font-size: 4.1rem;
        line-height: 1;
        position: relative;
    }
    .landing-button {
        display:block;
        padding: 0;
        padding-top: 40px;
        margin-bottom: 0;
    }
    .landingtextcontainer {
        position: relative;
    }
    h1::before {
        content: '';
        width: 100px;
        background-color: white;
        height: 2px;
        position: absolute;
        left: 0px;
        top: -90px;
    }
    .dimage {
        margin-left: 55px;
    }
    .image-family > img {
        display: none;
    }
    .image-family-desktop > img {
        display: block;
        background-position: top right;
    }
    .landingpage {
        flex-direction:row;
        justify-content:space-between;
        position: relative;
    }

    .dimage {
        margin-bottom:-300px;
    }
    .landingpage {
        padding: 180px;
    }
    .header-pattern-right {
        position: absolute;
        right: 0;
        top:0;
    }
    .header-pattern-left {
        position: absolute;
        background-position: left bottom;
        bottom: -250px;
        left: 0;

    }
}

@media screen and (max-width: 1200px) {
    .header-pattern-left, .header-pattern-right {
        display: none;
    }
}
.main {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
}
h2 {
    font-family: 'DM Serif Display', serif;
    font-size: 2.5rem;
    font-weight: 400;

}
p {
    font-family: 'Karla', sans-serif;
}
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 25px;
}
.container > h3 {
    margin: 20px 0;
}
.main-bottom {
    background-color: hsl(256, 26%, 20%);
    color: rgba(255, 255, 255, 0.8);
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
    text-align: center;
    margin: 100px 0;
}
.main-bottom > h2 {
    padding: 50px;
}
.main-button {
    padding:25px;
    z-index: 10;
}
.main-button > button {
    padding: 15px;
    color: white;
    border: 2px solid white;
    background-color: hsl(256, 26%, 20%);
    padding: 10px 25px;
    text-transform:uppercase;
    font-size: 1.1rem;
}
.main-button > button:hover {
    color: hsl(256, 26%, 20%);
    background-color: white;
    
}
.wrapper-cards {
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
}
.main-pattern {
    display: none;
}
@media screen and (min-width: 768px) {
    .main {
        position: relative;
        margin-top: 300px;
    }
    .main-pattern {
        display: block;
       
    }
    .wrapper-cards {
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-start;
        max-width: 1200px;
    }
    .wrapper-cards .container {
        text-align: left;
    }
   .main > h2 {
       align-self: flex-start;
       font-size: 4.2rem;
   }
   .main > h2::before {
       content: '';
       width: 100px;
       height: 2px;
       background-color: gray;
       position: absolute;
       top:-50px;
       left: 0;
   }
   .main-bottom {
       width: 100%;
       flex-direction: row;
       padding: 50px 0;
       position: relative;
   }
   .container {
       display: block;
       padding-right: 15px;
   }
   .main-pattern {
       position: absolute;
       right: 0;
   }
}

@media screen and (max-width: 1000px) {
    .main-pattern {
        display: none;
    }
}
.social-icons {
    padding: 25px 0;
}
.social-icons > img {
    padding: 0 7px;
}
.social-icons > img:hover {
    color: crimson;
}
.footer-link > h4 {
    color: rgba(0, 0, 0, 0.45);
    padding: 25px;

}
.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Karla', sans-serif;
    background-color: hsl(0, 0%, 98%);
    position: relative;
}
.footer-logo-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    z-index: 10;
}
.footer-link {
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    text-align: center;
}
.footer-link > a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.65);
    font-weight: 800;
    padding: 5px 0;
}
.footer-links {
    margin-bottom: 70px;
}
.attribution {
    margin: 10px 0;
}
@media screen and (min-width: 768px) {
    .footer {
        display: flex;
        margin-top: 50px;
        position: relative;
    }
    .footer-link {
        text-align: left;
    }
    .footer-link > h4 {
        padding: 25px 0;
    }
    .footer-links {
        display: flex;
        justify-content:space-between;
        width: 100%;
        margin: 50px 0;
    }
    .footer-logo-container {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        margin-top: 50px;
    }
    .footer-links::before {
        content: '';
        width: 980px;
        height: 2px;
        background-color: gray;
        position: absolute;
        top: 150px;
    }
    .footer-pattern {
        position: absolute;
        left: 0;
        top:0;
    }
}
